body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: gainsboro;
  min-height: 100vh;
  overflow: hidden;
  height: 3000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);

}


.App-header {
  text-align: center;
  width: 100%;
  height: inherit;
  display: block;
}

.App-link {
  color: #61dafb;
}

.thumbnail{
  display: inline-block;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Home{
    width: 100%;
    max-height: 100%;
    background-color: gainsboro;
    color: white;
    display: block; 
}

.About-container{

    width: 100%;
    height: 600px;
    background-color: rgb(243, 240, 240);
    position: relative;
    color: black;
   
   
}
h1{
    margin-top: 0px;
    margin-bottom:0px;
   
}
hr{
    width: 500px;
    border-top: 1px solid black;
}

.Name{
    font-size: 65pt;
    text-align: center;
    color: black;
    background-attachment: fixed;
}

.box-container{
    width:100%;
    height:75px;
    position: absolute;
    padding: 20px;
}
.box{
    width: 200px;
    height: 75px;
    background-color: red;
    position: relative;
    
}
.box.right{
    float: right;
}


.one{
    float: left;
    margin-left: 2%;
    position: absolute;
}
.two{
    position: absolute;
    float: center;
    width: 100%;
    text-align: center;
    margin-left: 15px;
    position: absolute;
}
.three{
    float: right;
    margin-right: 2%;
    position: relative;
}

.bio-enter{
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
}
.bio-enter-done{
    -webkit-transition: all 500ms linear;
    transition: all 500ms linear;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}
.bull-container{
    width: 100%;
    margin-top: 30px;
    position: relative;
}

.bio-text{
    color: black;
}
.bio-enter{
    color: gainsboro;
}

.Intro{
    width:100%;
    height: 550px;
    background-color: rgb(221, 194, 194);
    border-top: 1px solid black;
}
.myBanner-box{
    background-color: rgb(221, 194, 194);
    width: 100%;
    height: 500px;
    size: contain;
    position: relative;
    background-image: url(/mywebsite/static/media/circle.9fe921cb.png);
    background-repeat: no-repeat;
    background-size: 400px;
    background-position: top center;
    background-attachment:fixed;
    overflow: hidden;
}
.banner-mbox{
    width: 500px;
    height:400px;
    left: 50%;
    top: 50%;
    margin-left:-250px;
    margin-top: -475px;
    position: absolute;
    background-image: url(/mywebsite/static/media/circle-img-2.98b54626.png);
    background-size: 500px;
    background-repeat: no-repeat;
    
}
.banner-fbox{
    left: 50%;
    top: 0%;
    margin-left: -80px;
    margin-top: -290px;
    width: 200px;
    height: 200px;
    background-image: url(/mywebsite/static/media/circle-image-3.416827d2.png);
    background-size: 150px;
    background-repeat: no-repeat;
    position: absolute;
    
}

.scroll-text{
    z-index: 999;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width:100px;
    height:20px;
    position: relative;
    /*background-color: pink;*/
    text-align: center;
    font-size:13pt;
    color: black;
}
.scroll-icon{
    width: 40px;
    height:inherit;
    background-color: transparent;
    background-image: url(/mywebsite/static/media/scroll-icon.0e7b08e7.png);
    background-size: 30px;
    background-position: center;
    float: right;
}
.top-offset{
    width: 100%;
    height:20px;
    background-color: rgb(243, 240, 240);
    display: inline-block;
}
.triangle{
    width: 100%;
    height: 10px;
   
    background-color:rgb(221, 194, 194);
    display: inline-block;
    -webkit-clip-path: polygon(100% 0, 0 100%, 100% 100%);
            clip-path: polygon(100% 0, 0 100%, 100% 100%);
}
/**************************************/
.Projects-container{
    width: 100%;
    height: 800px;
    background-image: -webkit-linear-gradient(top, rgb(221, 194, 194) , rgb(221, 194, 194) , rgb(250, 186, 114));
    background-image: linear-gradient(top, rgb(221, 194, 194) , rgb(221, 194, 194) , rgb(250, 186, 114));
    position: relative;
    -webkit-clip-path: polygon(51% 100%, 100% 83%, 100% 0, 0 -20%, 0 83%);
            clip-path: polygon(51% 100%, 100% 83%, 100% 0, 0 -20%, 0 83%);
}

.Projects{
    position: absolute;
}
.title-parent{
    display: block;
    width: 100%;
    text-align: center;
    position: absolute;
}
.title-container{
    display: inline-block;
    font-size: 60pt;
    margin-right: 12px;
    margin-bottom: 15px;

}
.t{
    display: inline-block;
}
.pro{
    display: block;
    margin: auto;
    width: 250px;
    left: 20%;
    top: 18%;
    position: absolute;
}

/************************************/
.P-enter{
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    color:black;
}
.P-enter-done{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    -webkit-transition: all 300ms ease-in;
    transition: all 300ms ease-in;
    position: relative;
    color:black;
}
.R-enter{
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    color:black;
}
.R-enter-done{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    -webkit-transition: all 300ms ease-in;
    transition: all 300ms ease-in;
    position: relative;
    color:black;
}
/************************************/
.content-container{
    width: 100%;
    height: 450px;
}
.content-enter{
    opacity: 0;
}
.content-enter-done{
    opacity: 1;
    -webkit-transition: opacity 250ms linear;
    transition: opacity 250ms linear;
    -webkit-transition-delay: 250ms;
            transition-delay: 250ms;
}

.project-card{
    width: 250px;
    height: 115px;
    border: 1px solid white;
    border-radius: 2px;
    margin: 10px;
    display: inline-block;
}

.row{
    display: block;
    text-align: center;
}

.col{
    display: inline-block;
    margin: 5px;
    border: 1px solid rgb(155, 151, 151) ;
    background-color: rgb(243, 240, 240);
    height: 180px;
    width: 300px;
    max-width: 300px;
    min-width: 300px;
    border-radius: 6px;
    text-align: left;
    padding: 10px;
    font-size: 12pt;
}
.col:hover{
    border: 1px solid black;
}

.my-hr{
    width: 500px;
    height: 1px;
    background-color: black;
    display: block;
    margin: auto;
    margin-bottom: 15px;
}
.description{
    display: block;
    margin: auto;
    margin-top: 3%;
    width: 98%;
    height: 60%;
    background-color: transparent;
    font-size: 10pt;
    color: black;
}
a:hover{
    text-decoration: underline;
}
a{
    color: rgb(61, 61, 224);
    font-size: 14pt;
    font-weight: 600;
}
.dot{
    margin-top:5px;
    width: 12px;
    height: 12px;
    background-color: blue;
    -webkit-clip-path: circle(6px at center);
            clip-path: circle(6px at center);
    display: inline-block;
    margin-right: 10px;
}
.dot.java{
    background-color: rgb(228, 228, 77);
}
.dot.react{
    background-color: rgb(73, 73, 231);
}
.dot.python{
    background-color: rgb(54, 163, 50);
}
.dot-text{
    display: inline-block;
    color: black;
}
.dot-parent{
    margin-top: 2px;
    background-color: transparent;
}
